"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Separator } from "@/components/ui/separator"
import { useAuth } from "@/contexts/AuthContext"
import { useToast } from "@/components/ui/use-toast"
import toastService from "@/services/toastService"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { InfoIcon } from "lucide-react"
import apiClient from "@/services/apiClient"

// Interface para o objeto de permissão
interface Permissao {
  id?: number
  nome?: string
  descricao?: string
}

export default function DiagnosticoAutenticacao() {
  const { user, isAuthenticated, refreshAuth, logout } = useAuth()
  const [token, setToken] = useState<string | null>(null)
  const [backendStatus, setBackendStatus] = useState<"checking" | "ok" | "error">("checking")
  const { toast } = useToast()

  useEffect(() => {
    // Registrar a função toast no serviço
    toastService.register(toast)

    // Obter o token do localStorage
    if (typeof window !== "undefined") {
      setToken(localStorage.getItem("token"))
    }

    // Verificar se os endpoints de autenticação estão implementados
    checkBackendEndpoints()
  }, [toast])

  const checkBackendEndpoints = async () => {
    try {
      // Verificar se o endpoint /api/auth/me está implementado
      const token = localStorage.getItem("token")
      if (!token) {
        setBackendStatus("error")
        return
      }

      // Usar apiClient para verificar o endpoint
      await apiClient.get("/auth/me")
      setBackendStatus("ok")
    } catch (error) {
      console.error("Erro ao verificar endpoints de autenticação:", error)
      setBackendStatus("error")
    }
  }

  const handleRefreshAuth = async () => {
    try {
      // Verificar se há token antes de tentar renovar
      if (typeof window !== "undefined" && !localStorage.getItem("token")) {
        toast({
          title: "Não autenticado",
          description: "Você precisa estar autenticado para renovar o token.",
          variant: "destructive",
        })
        return
      }

      await refreshAuth()
      toast({
        title: "Autenticação atualizada",
        description: "Suas informações de autenticação foram atualizadas com sucesso.",
      })

      // Atualizar o token exibido
      if (typeof window !== "undefined") {
        setToken(localStorage.getItem("token"))
      }
    } catch (error) {
      console.error("Erro ao renovar autenticação:", error)

      toast({
        title: "Erro ao atualizar autenticação",
        description: "Não foi possível atualizar suas informações de autenticação.",
        variant: "destructive",
      })
    }
  }

  const handleClearAuth = () => {
    if (typeof window !== "undefined") {
      localStorage.removeItem("token")
      localStorage.removeItem("user")
      setToken(null)
      toast({
        title: "Autenticação limpa",
        description: "Suas informações de autenticação foram removidas.",
      })
      // Recarregar a página para atualizar o estado
      window.location.reload()
    }
  }

  const handleTestToast = () => {
    toastService.success("Teste de Toast", "Este é um teste do serviço de toast.")
  }

  const handleLogin = () => {
    // Redirecionar para a página de login
    window.location.href = "/login"
  }

  // Função para extrair o nome da permissão
  const getPermissionName = (perm: string | Permissao): string => {
    if (typeof perm === "string") {
      return perm
    }

    if (perm && typeof perm === "object") {
      return perm.nome || "Permissão sem nome"
    }

    return "Permissão desconhecida"
  }

  return (
    <div className="container mx-auto py-8">
      <h1 className="text-2xl font-bold mb-6">Diagnóstico de Autenticação</h1>

      {backendStatus === "error" && (
        <Alert className="mb-6">
          <InfoIcon className="h-4 w-4" />
          <AlertTitle>Aviso de configuração</AlertTitle>
          <AlertDescription>
            Os endpoints de autenticação (/api/auth/me e /api/auth/refresh) podem não estar implementados corretamente
            no backend. Isso pode causar problemas na renovação automática de token. Verifique a implementação desses
            endpoints no backend.
          </AlertDescription>
        </Alert>
      )}

      <div className="grid gap-6">
        <Card>
          <CardHeader>
            <CardTitle>Status da Autenticação</CardTitle>
            <CardDescription>Informações sobre o estado atual da autenticação</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div>
                <p className="font-semibold">Status:</p>
                <p className={isAuthenticated ? "text-green-600" : "text-red-600"}>
                  {isAuthenticated ? "Autenticado" : "Não autenticado"}
                </p>
              </div>

              {user && (
                <>
                  <Separator />
                  <div>
                    <p className="font-semibold">Usuário:</p>
                    <p>ID: {user.id}</p>
                    <p>Nome: {user.nome}</p>
                    <p>Username: {user.username}</p>
                    <p>Perfil: {user.perfil}</p>
                  </div>

                  <Separator />
                  <div>
                    <p className="font-semibold">Permissões:</p>
                    {user.permissoes && user.permissoes.length > 0 ? (
                      <ul className="list-disc pl-5">
                        {user.permissoes.map((perm: string | Permissao, index: number) => (
                          <li key={index}>{getPermissionName(perm)}</li>
                        ))}
                      </ul>
                    ) : (
                      <p>Nenhuma permissão encontrada</p>
                    )}
                  </div>
                </>
              )}

              <Separator />
              <div>
                <p className="font-semibold">Token:</p>
                {token ? (
                  <div>
                    <p className="text-xs break-all bg-gray-100 p-2 rounded">{token}</p>
                    <p className="text-sm mt-2">Comprimento: {token.length} caracteres</p>
                  </div>
                ) : (
                  <p className="text-red-600">Nenhum token encontrado</p>
                )}
              </div>
            </div>
          </CardContent>
          <CardFooter className="flex flex-wrap gap-2">
            {isAuthenticated ? (
              <>
                <Button onClick={handleRefreshAuth}>Renovar Autenticação</Button>
                <Button variant="destructive" onClick={logout}>
                  Logout
                </Button>
              </>
            ) : (
              <Button onClick={handleLogin}>Fazer Login</Button>
            )}
            <Button variant="outline" onClick={handleClearAuth}>
              Limpar Autenticação
            </Button>
            <Button variant="secondary" onClick={handleTestToast}>
              Testar Toast
            </Button>
          </CardFooter>
        </Card>
      </div>
    </div>
  )
}
